.container {
  background-color: rgb(78, 204, 226);
  display: flex;
  flex-direction: column;
  
  .header {
    font-weight: 500;
    font-size: 32px;
    padding: 16px;
  }
  .subnav {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex-grow: 1;
      padding: 16px 0;

     .subnavSection {


      .subnavOption {
        display: block;
        text-decoration: none;
        padding: 8px 16px;
        cursor: pointer;
        user-select: none;

        &:hover{
          background-color: aquamarine;
        }

        &:active{
          background-color: cadetblue;
        }
      }
      
      .selected {
        background-color: aqua;
      }
     }
  }

}

